module.exports = "#rig {\n    max-width:1080px;\n    margin:0 auto; /*center aligned*/\n    padding:0;\n    font-size:0; /* Remember to change it back to normal font size if have captions */\n    list-style:none;\n    background-color:#fff;\n}\n#rig li {\n    display: inline-block;\n    *display:inline;/*for IE6 - IE7*/\n    width:100%;\n    vertical-align:middle;\n    box-sizing:border-box;\n    -moz-box-sizing: border-box;\n\t-webkit-box-sizing: border-box;\n    margin:0;\n    padding:10px 10px 5% 10px;\n}\n/* The wrapper for each item */\n.rig-cell {\n    /*margin:12px;\n    box-shadow:0 0 6px rgba(0,0,0,0.3);*/\n    display:block;\n    position: relative;\n    overflow:hidden;\n    box-shadow: 0px 0px 10px #00000042;\n}\n.rig-title-post{padding-top: 10px; padding-bottom: 10px; height: 86px; line-height: 1;}\n/* If have the image layer */\n.rig-img {\n    display:block;\n    width: 100%;\n    height: 300px;\n    border:none;\n    transform:scale(1);\n    transition:all 1s;\n    -o-object-fit: cover;\n       object-fit: cover;\n}\n#rig li:hover .rig-img {\n    transform:scale(1.05);\n}\n/* If have the overlay layer */\n.rig-overlay {\n    position: absolute;\n    display:block;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    margin: auto;\n    background: #fff no-repeat center 20%;\n    background-size:50px 50px;\n    opacity:0;\n    filter:alpha(opacity=0);/*For IE6 - IE8*/\n    transition:all 0.6s;\n}\n#rig li:hover .rig-overlay {\n    opacity:0;\n}\n/* If have captions */\n.rig-text{text-align: left; color: #000; font-size: 20px; line-height: 1; font-weight: 600;}\n#rig li:hover .rig-text {\n    transform:translateY(0px);\n    opacity:0.9;\n}\n@media (max-width: 9000px) {\n    #rig li {\n        width:100%;\n    }\n}\n@media (max-width: 700px) {\n    #rig li {\n        width:100%;\n    }\n}\n@media (max-width: 550px) {\n    #rig li {\n        width:100%;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"